// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import SimpleMDE from 'simplemde'

import VueSimplemde from 'vue-simplemde'

import App from './App'
import router from './router'
import store from './store'

//import 'bulma/css/bulma.css'

// don't parse html
SimpleMDE.prototype._markdown = SimpleMDE.prototype.markdown;
SimpleMDE.prototype.markdown = function( text ) {   

  let newText = text.split(''),
      curorPos = this.simple

  let inCode = false;
  for( let i = 0; i < text.length; ++i ) {
   
    let char = text[ i ];

    // this actually works because the number of ticks is always odd
    if( char === '`' && ( text.lastIndexOf( '`' ) !== i && inCode ) )
      inCode = !inCode;

    if( char === '<' && !inCode )
      newText[ i ] = '&lt;';
  }

  var next = this._markdown( newText.join('') );

  return next;

}

Vue.config.productionTip = false;
Vue.config.devtools = true;

Vue.use( VueSimplemde );

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  created() {
    this.$store.dispatch( 'populateAuthentication' );
  },
  template: '<App/>',
  components: { App }
});




